/* - - - START RESET STYLES - - - */
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html {
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: subpixel-antialiased !important;
}

html,
body {
  font-size: 100%;
  font-size: 16px;
  line-height: 1;
  margin: 0;
  padding: 0;
  width: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input {
  outline: none;
  border: none;
}

input::-ms-clear {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  display: inline-block;
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  select:focus,
  textarea:focus,
  input:focus {
    font-size: 16px;
  }
}
/* - - - END RESET STYLES - - - */
/* - - - FONTS START - - - */
@font-face {
  font-family: "AvenirNextCyr";
  src: url("../fonts/AvenirNextCyr-Regular.ttf") format("ttf"), url("../fonts/AvenirNextCyr-Regular.woff") format("woff"), url("../fonts/AvenirNextCyr-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "AvenirNextCyr";
  src: url("../fonts/AvenirNextCyr-Bold.ttf") format("ttf"), url("../fonts/AvenirNextCyr-Bold.woff") format("woff"), url("../fonts/AvenirNextCyr-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ArialBold";
  src: url("../fonts/Arial-BoldMT.ttf") format("ttf"), url("../fonts/Arial-BoldMT.woff") format("woff"), url("../fonts/Arial-BoldMT.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
/* - - - FONTS END - - */
body {
  background-color: #eee;
  color: #333;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5rem;
  cursor: default;
}

.container {
  background-color: #fff;
  max-width: 100%;
  margin: 0 auto;
  width: 30rem;
}

img {
  max-width: 100%;
  height: 100%;
}

.btn {
  border: 0;
  border-radius: 0.25rem;
  background: #ffd800;
  box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  color: #333;
  cursor: pointer;
  display: block;
  font-family: "ArialBold", sans-serif;
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 5rem;
  letter-spacing: 0.125rem;
  margin: 0 auto;
  max-width: 25rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: box-shadow 0.3s, color 0.5s;
  width: 100%;
}
.btn:hover {
  box-shadow: 0 0 0.9375rem rgba(0, 0, 0, 0.3);
  color: #149cc7;
}
.btn:active {
  box-shadow: none;
  color: #333;
}
.btn:focus {
  box-shadow: 0 0 0.9375rem rgba(0, 0, 0, 0.3);
  color: blueviolet;
}

.error {
  position: relative;
}

.error .field {
  border-color: #ee3737;
}

.error-label {
  bottom: 2.5625rem;
  color: red;
  display: block;
  font-family: "Arial", sans-serif;
  font-weight: 700;
  left: 25%;
  position: absolute;
}

/* --- OFFER BLOCK START --- */
.offer-title {
  font-family: "ArialBold", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}
.offer-title__text {
  background-color: #149cc7;
  color: #fff;
  font-size: 2rem;
  line-height: 2.25rem;
  padding: 0.625rem 0 2.625rem;
}

/* --- TIMER-BLOCK START --- */
.timer-block {
  display: flex;
  justify-content: space-between;
  padding: 0 0.625rem 0.4375rem 1.25rem;
}
.timer-block__text {
  background: url(../images/icons/offer__clock_icon.webp) left 12% no-repeat;
  font-size: 0.875rem;
  line-height: 1rem;
  margin: 0.9375rem 0 0;
  max-width: 10.625rem;
  padding: 0 0 0 2.5rem;
  width: 100%;
}

.timer {
  display: flex;
  width: 53%;
}
.timer__item {
  flex-basis: 25%;
  text-align: center;
}
.timer__item .text {
  font-size: 0.875rem;
}
.timer__item:not(:last-child) .count::after {
  content: ":";
  display: block;
  line-height: 1.5rem;
  position: absolute;
  right: -0.3125rem;
  top: 0;
  width: 0.625rem;
}

.count {
  font-family: "ArialBold", sans-serif;
  font-weight: 700;
  font-size: 1.875rem;
  line-height: 1.875rem;
  position: relative;
}

/* --- TIMER-BLOCK END --- */
/* --- BENEFITS-TEXT START --- */
.benefits-text {
  background-color: #149cc7;
  color: #fff;
  display: flex;
  font-size: 0.875rem;
  line-height: 1rem;
  min-height: 3.5rem;
  padding: 0.75rem 0 0.75rem 1.125rem;
}
.benefits-text__item {
  max-width: 160px;
  width: 100%;
  padding: 0 0 0 44px;
  position: relative;
}
.benefits-text__item::before {
  background: rgba(0, 0, 0, 0.1) center no-repeat;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  content: "";
  display: block;
  left: 0;
  min-height: 2.25rem;
  position: absolute;
  top: -0.125rem;
  max-width: 2.25rem;
  width: 100%;
}
.benefits-text__item:nth-child(1)::before {
  background-image: url(../images/icons/benefit_icon-1.webp);
}
.benefits-text__item:nth-child(2)::before {
  background-image: url(../images/icons/benefit_icon-2.webp);
}
.benefits-text__item:nth-child(3)::before {
  background-image: url(../images/icons/benefit_icon-3.webp);
}

/* --- BENEFITS-TEXT END --- */
/* --- PRICE-BLOCK START --- */
.price-block {
  border-bottom: 1px solid #e6e6e6;
  display: flex;
}

.price__item {
  align-items: center;
  display: flex;
  flex-basis: 50%;
  flex-flow: column nowrap;
  font-size: 0.875rem;
  min-height: 5.625rem;
  line-height: 0.875rem;
  padding: 0.9375rem 0 0 0;
}
.price__item .text {
  padding-top: 0.3125rem;
  margin-bottom: 0.1875rem;
}

.old-price {
  border-right: 1px solid #e6e6e6;
}
.old-price .value {
  font-family: "ArialBold", sans-serif;
  font-weight: 700;
  font-size: 1.875rem;
  line-height: 1.875rem;
  text-decoration: line-through;
}

.new-price .text {
  background: #ee3737;
  border-radius: 0.25rem;
  color: #fff;
  padding: 0.3125rem 0.3125rem 0.3125rem;
}
.new-price .value {
  font-family: "ArialBold", sans-serif;
  font-weight: 700;
  font-size: 2.25rem;
  line-height: 2.25rem;
}

/* --- PRICE-BLOCK END --- */
.general-list {
  margin: 1.5625rem 2.5rem 0;
}
.general-list__item {
  background: url(../images/icons/tick_icon.webp) left 0.0625rem no-repeat;
  padding: 0 0 0 2rem;
}
.general-list__item:not(:last-child) {
  margin: 0 0 0.75rem;
}

.offer__link {
  margin: 1.5625rem auto 1.25rem;
}

.products-count {
  margin: 1.25rem 0 0;
  text-align: center;
}
.products-count b {
  background: #149cc7;
  border-radius: 0.25rem;
  color: #fff;
  margin: 0 0.1875rem;
  padding: 0.1875rem 0.3125rem;
}

/* --- OFFER BLOCK END --- */
/*--- BUYER-PREPARATION START ---*/
.buyer-preparation {
  margin: 1.5625rem 0 0 0;
}
.buyer-preparation__text {
  font-size: 1rem;
  text-align: center;
  padding: 0.625rem 0;
}

.title {
  font-family: "ArialBold", sans-serif;
  font-weight: 700;
  font-size: 1.875rem;
  line-height: 2.25rem;
  margin-bottom: 1.875rem;
  padding: 0 0.625rem;
  text-align: center;
}

/*--- BUYER-PREPARATION END ---*/
/*--- APPLICATION-METHOD START ---*/
.image-prod-4,
.image-prod-5 {
  display: block;
}

.image-prod-4 {
  margin: 1.25rem auto 0;
}

.image-prod-5 {
  margin: 1.25rem auto 1.5625rem;
}

/*--- APPLICATION-METHOD END ---*/
/*--- BENEFITS START ---*/
.benefits {
  background-color: #333;
  color: #fff;
  padding: 1.875rem 0 0.625rem;
}
.benefits-item {
  display: flex;
  margin: 0 1.25rem 1.875rem;
  max-width: 27.5rem;
  width: 100%;
  min-height: 9.375rem;
}
.benefits-item_reverse {
  flex-flow: row-reverse;
}
.benefits-item__text {
  padding: 0 0 0 1.25rem;
}
.benefits-item__text_reverse {
  padding: 0 1.25rem 0 0;
  text-align: right;
}

.image-prod {
  display: block;
  border-radius: 0.25rem;
  max-width: 9.375rem;
}

.title-yellow {
  color: #ffd800;
  font-family: "ArialBold", sans-serif;
  font-weight: 700;
  margin: 0 0 0.3125rem;
}

/*--- BENEFITS END ---*/
/*--- DESCRIPTION-VIDEO START ---*/
.description-video {
  padding: 1.875rem 0;
}

.title_blue {
  color: #149cc7;
}

/*--- DESCRIPTION-VIDEO END ---*/
/*--- OFFER-BLUE START ---*/
.offer-blue {
  background-color: #149cc7;
  color: #fff;
  padding: 1.875rem 0 0;
}
.offer-blue .general-list {
  margin: 0;
  padding: 0;
}
.offer-blue .general-list__item {
  background: url(../images/icons/yellow_tick_icon.webp) 1.25rem 0.125rem no-repeat;
  color: #fff;
  padding: 0 1.25rem 1rem 3.125rem;
  position: relative;
}
.offer-blue .general-list__item:not(:last-child) {
  margin: 0 0 0.75rem;
}
.offer-blue .general-list__item:not(:last-child)::before {
  background: url(../images/icons/separator__icon.webp) center no-repeat;
  bottom: 0;
  content: "";
  display: block;
  height: 0.25rem;
  left: 0;
  position: absolute;
  width: 100%;
}
.offer-blue .general-list__item:last-child {
  padding-bottom: 0;
}
.offer-blue .products-count b {
  background: #107d9f;
}
.offer-blue__text {
  background-color: #fff;
  color: #333;
  font-size: 1rem;
  padding: 0.625rem 0;
  text-align: center;
}

.image-prod-9 {
  display: block;
  margin: 0 auto 30px;
}

.title_white {
  color: #fff;
}

.image-prod-10 {
  margin: 1.5625rem 0 3.75rem;
}

/*--- OFFER-BLUE END ---*/
.customer-reviews {
  background-color: #149cc7;
  padding: 1.875rem 1.25rem 2.5rem;
  text-align: center;
}
.customer-reviews__info {
  padding: 1.25rem 1.875rem 4.1875rem;
}
.customer-reviews__title {
  margin: 0 0 0.625rem;
  font-family: "Arial", sans-serif;
  font-weight: 700;
}

.slider {
  background-color: #fff;
  border-radius: 0.25rem;
  box-shadow: 0 -3px 0 #1185a9 inset;
  min-height: 32.6875rem;
  position: relative;
}
.slider__show {
  height: 37.5rem;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  width: 27.5rem;
}
.slider__row {
  display: flex;
  left: -27.5rem;
  position: absolute;
  top: 0;
  width: 82.5rem;
  transition: all 0.5s ease 0s;
}
.slider__cell {
  width: 27.5rem;
  height: 37.5rem;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}
.slider__cell .customer-reviews__info,
.slider__cell picture {
  flex-basis: 50%;
}
.slider__cell picture {
  height: 60%;
}
.slider-dots {
  bottom: 3%;
  display: flex;
  font-size: 0;
  justify-content: space-between;
  left: 50%;
  letter-spacing: -0.0625rem;
  line-height: 0;
  max-width: 4.5625rem;
  position: absolute;
  text-align: center;
  transform: translateX(-50%);
  width: 100%;
  z-index: 2;
}
.slider-dots__item {
  background: #333;
  border-radius: 50%;
  display: inline-block;
  height: 0.625rem;
  margin: 0 0.25rem;
  vertical-align: middle;
  width: 0.625rem;
}
.slider-dots__item_active {
  background: #149cc7;
  border-radius: 0.3125rem;
  width: 1.25rem;
}
.slider__arrows {
  height: 37.5rem;
  left: 50%;
  position: absolute;
  top: 0;
  transform: translateX(-50%);
  width: 27.5rem;
}
.slider__arrows_left, .slider__arrows_right {
  background: #149cc7 url(../images/slider/slider__arrows.webp) no-repeat;
  border-radius: 50%;
  cursor: pointer;
  display: block;
  height: 2.5rem;
  max-width: 2.5rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
}
.slider__arrows_left {
  background-position: left center;
  left: -1.25rem;
}
.slider__arrows_right {
  background-position: right center;
  right: -1.25rem;
}

.delivery-payment__title {
  margin-top: 1.875rem;
}
.delivery-payment__item {
  border-top: 0.25rem solid #d9d9d9;
  display: flex;
  justify-content: space-between;
  margin: 0;
  min-height: 12.75rem;
  width: 100%;
}
.delivery-payment__item:nth-child(odd) .delivery-payment__text {
  padding: 0 1.25rem 0 0;
}
.delivery-payment__item:nth-child(even) .delivery-payment__text {
  padding: 0 0 0 1.25rem;
}
.delivery-payment__item_reverse {
  flex-flow: row-reverse;
}
.delivery-payment__text {
  align-items: center;
  display: flex;
  flex-basis: 50%;
  flex-flow: column;
  justify-content: center;
  text-align: center;
}
.delivery-payment__text-title {
  color: #149cc7;
  font-family: "Arial", sans-serif;
  font-weight: 700;
  margin: 0 0 0.3125rem;
}

.img-delivery {
  max-width: 13.75rem;
  width: 100%;
}

footer .offer {
  padding: 0 0 1.875rem 0;
}

.footer-block {
  align-items: center;
  background: #f2f2f2;
  display: flex;
  flex-flow: column;
  padding: 1.5625rem 0;
  /* font-size: 14px; */
  /* line-height: 22px; */
  text-align: center;
}
.footer-block a {
  color: #111;
  font-size: 0.875rem;
  line-height: 1.375rem;
  text-decoration: underline;
  width: fit-content;
}

.order-form {
  display: flex;
  flex-flow: column nowrap;
  row-gap: 20px;
}

.field {
  background-color: #f5f5f5;
  background-repeat: no-repeat;
  border: 1px solid #e6e6e6;
  border-radius: 0.25rem;
  color: #333;
  display: block;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  margin-left: auto;
  margin-right: auto;
  max-width: 25rem;
  padding: 1.4375rem 1.25rem 1.4375rem 5rem;
  width: 100%;
}
.field::placeholder {
  color: #333;
}
.field:focus {
  box-shadow: 0 0 0.9375rem rgba(0, 0, 0, 0.3);
}
.field:focus::placeholder {
  color: transparent;
}

.order-form__input-name {
  margin-top: 1.5625rem;
  background-image: url(../images/icons/offer__name_icon.webp);
  background-position: 23px center;
}

.order-form__input-phone {
  background-image: url(../images/icons/offer__phone_icon.webp);
  background-position: 20px center;
}